<template>
	<view class="">
		<uni-nav>
			<view slot="center">
				个人中心
			</view>
			<view slot="right" class="iconfont icon-ziyuan more" v-if="user!=''" @click="handleMore"></view>
		</uni-nav>
		<view class="user_info_row" @click="handleLogin">
			<block v-if="user==''">
				<text class="login">登录</text>
			</block>
			<block v-if="user!=''"> 
				<view class="title">
					欢迎登陆
				</view>
				<view class="user_info">
					<image class="user_avatar" :src="user.user_avatar" mode="aspectFill"></image>
					<view class="user_desc">
						<view class="user_name">{{user.user_name}}</view>
						<view class="user_address">{{user.user_address}}</view>
					</view>
				</view>
			</block>
		</view>
		<view class="content">
			<view class="main">
				<!-- 历史足迹 -->
				<uni-history :collectNum='collectNum' @collect='collect'></uni-history>
				
				<!-- 我的订单 -->
				<uni-order @handlePay='handlePay'></uni-order>
				
				<!-- 收货地址 -->
				<uni-address></uni-address>
		
				<!-- 应用信息 -->
				<uni-app-info></uni-app-info>

			</view>
		</view>
		
		<uni-popup ref="popup" type="bottom" class="popup_box" maskClick="false">
			<view class="quit">
				退出账号
			</view>
			<view class="cancel">
				取消
			</view>
		</uni-popup>
	</view>
</template>

<script>
	import uniNav from '../../component/uni-nav.vue'
	
	import uniHistory from './components/uniHistory.vue'
	import uniOrder from './components/uniOrder.vue'
	import uniAddress from './components/uniAddress.vue'
	import uniAppInfo from './components/uniAppInfo.vue'
	
	import uniPopup from '@/components/uni-popup/uni-popup.vue'
	import uniPopupMessage from '@/components/uni-popup/uni-popup-message.vue'
	import uniPopupDialog from '@/components/uni-popup/uni-popup-dialog.vue'
	export default {
		data(){
			return{
				user:{},
				collectNum:0
			}
		},
		components: {
			uniNav,
			uniHistory,
			uniOrder,
			uniAddress,
			uniAppInfo,
			
			uniPopup,
			uniPopupMessage,
			uniPopupDialog
		},
		onShow() {
			this.user=uni.getStorageSync('user')
			console.log(this.user=='','-----')
			this.collectNum=uni.getStorageSync('collect').length
		},
		methods:{
			handleLogin(){
				uni.login({
					provider:'weixin',
					success: function (loginRes) {
					    console.log(loginRes.authResult);
					    // 获取用户信息
					    uni.getUserInfo({
					      provider: 'weixin',
					      success: function (res) {
					        const user_name=res.userInfo.nickName
							const user_address=res.userInfo.country+'-'+res.userInfo.province+'-'+res.userInfo.city
							const user_avatar=res.userInfo.avatarUrl
							this.user={
								user_name,
								user_address,
								user_avatar
							}
							console.log(this.user)
							uni.setStorageSync('user',this.user)
					      }
					    });
					  }
				})
			},
			collect(){
				uni.navigateTo({
					url:'../collect/collect'
				})
			},
			handlePay(){
				uni.navigateTo({
					url:'../pay/pay'
				})
			},
			handleMore(){
				console.log('11')
				this.$refs.popup.open()
			}
		}
	}
</script>

<style lang="scss">
	@import '../../static/iconfont/iconfont.css';

	page {
		background: #edece8;
	}
	.more{
		font-size: 60rpx;
	}
	.popup_box{
		width: 100%;
		height: 200rpx;
		background-color: #fff;
	}
	.user_info_row {
		width: 100%;
		height: 600rpx;
		background-color: #ff5777;
		position: relative;
	}
	.title{
		height: 150rpx;
		position: absolute;
		left: 50rpx;
		right: 50rpx;
		top: 200rpx;
		color: #fff;
		font-size: 46rpx;
		font-weight: 900;
	}
	.user_info{
		height: 150rpx;
		position: absolute;
		left: 50rpx;
		right: 50rpx;
		top: 300rpx;
		display: flex;
		// background-color: purple;
		background-color: #ccc;
		border-radius: 20rpx;
	}
	.user_avatar{
		margin: 30rpx;
		height: 100rpx;
		flex: 1;
		border-radius: 50%;
	} 
	.user_desc{
		color: #fff;
		flex: 5;
		display: flex;
		flex-direction: column;
	}
	.user_name{
		flex: 1;
		display: flex;
		align-items: center;
	}
	.user_address{
		flex: 1;
		display: flex;
		align-items: center;
		font-size: 28rpx;
	}
	.login {
		width: 230rpx;
		height: 90rpx;
		line-height: 90rpx;
		text-align: center;
		color: #fff;
		border: 1rpx solid #4CD964;
		box-shadow: 0 0 6rpx #4CD964;
		border-radius: 10rpx;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
	}

	.content {
		position: relative;
	}

	.main {
		width: 90%;
		position: absolute;
		left: 50%;
		transform: translateX(-50%);
		top: -30rpx;
	}



</style>
